html {
	box-sizing: border-box;
	font-size: $main-fz;
}
html,
body {
	height: 100%;
}
*,
*::before,
*::after {
	box-sizing: inherit;
}
body {
	margin: 0;
	padding: 0;
	font-family: $main-font;
	color: $main-color;
}
// start reset CSS
h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
	padding: 0;
	text-transform: capitalize;
}

input,
button,
textarea,
select {
	border: none;
}
button {
	padding: 0;
	background-color: transparent;
	cursor: pointer;
}
textarea {
	resize: none;
}
ul,
li {
	list-style: none;
	margin: 0;
	padding: 0;
}
a {
	display: inline-block;
	text-decoration: none;
	cursor: pointer;
}
// end reset CSS

.wrapper {
	display: flex;
	flex-direction: column;
	position: relative;
	height: 100vh;
	overflow: hidden;
}
.content {
	flex: 1 0 auto;
}
.footer {
	flex: 0 0 auto;
}
.container-fluid {
	max-width: 1800px;
	padding: 0 15px;
	margin: 0 auto;
}
.container {
	max-width: 1460px;
	padding: 0 15px;
	margin: 0 auto;
}
.img {
	background-size: cover;
	background-position: top center;
	background-repeat: no-repeat;
}
.coll-number {
	color: $main-grey;
	line-height: 30px;
	font-size: rem(14px);
	display: inline-block;
	width: 60px;
}
.row {
	display: flex;
	&:first-child {
		margin-bottom: rem(40px);
	}
	&:last-child {
		align-items: flex-end;
	}
}

//  попробовать это генерировать через JS сложность в том чтобы добраться до псевдоелементов getComputedStyle и нужно ли это генерировать с JS ??
.model-page {
	&::before,
	&::after {
		content: "";
		position: absolute;
		z-index: -1;
		height: 0;
		width: 0;
		transition: all 3s ease;
	}
	&-0000 {
		&::before {
			background-color: $main-blue;  
		}
		&::after {
			background-color: $main-pink;
		}
	}
	// colors on different design
	&-0001 {
		&::before {
			background-color: $second-green;
		}
		&::after {
			background-color: $second-pink;	
		}
	}
	&-0002 {
		&::before {
			background-color: $third-purple;
		}
		&::after {
			background-color: $white;	
		}
		// #c4b7d2
	}
	// end
	&--vertical {
		&::before,
		&::after {
			top: 0;
			height: 100%;
			width: 50%;
			animation: defaultParties 1s ease-in forwards;
			// transition: all 3s ease;
			
		}
		&::before {
			right: 0;
			transform: translateX(100%);
		}
		&::after {
			left: 0;
			transform: translateX(-100%);
		}
	}
	&--horizontal {
		&::before,
		&::after {
			left: 0;
			height: 50%;
			width: 100%;
			transition: all 3s ease;
		}
		&::before {
			bottom: 0;
			
		}
		&::after {
			top: 0;
		}
	}
	&--horizontal-mode-1 {
		&::before,
		&::after {
			left: 0;
			width: 100%;
		}
		&::before {
			bottom: 0;
			height: 80%;
		}
		&::after {
			top: 0;
			height: 20%;
		}
	}
	&--horizontal-mode-2 {
		&::before,
		&::after {
			left: 0;
			width: 100%;
		}
		&::before {
			bottom: 0;
			height: 20%;
		}
		&::after {
			top: 0;
			height: 80%;
		}
	}
	
}
.phone {
	font-size: rem(20px);
  color: $main-color;
}

.gallery {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

.model-socials {
	li {
    display: inline-block;
    & + li {
      margin-left: 15px;
    }
    a {
      color: $main-color;
      padding: 3px;
      font-size: (18px);
      position: relative;
      &::before {
        content: "";
        position: absolute;
        bottom: 3px;
        right: 0;
        height: 1px;
        width: 0;
        background-color: $main-color;
        transition: all 0.3s ease-out;
      }
      &:hover::before {
        left: 0;
        width: 100%;
      }
    }
  }
}

